.project-navbar {
    width: 30%;
    height: 100%;
    border-right: 1px solid black;
    box-shadow: 15px 0 9px -15px #1f2235;
    color: black;
}

.project-navbar-list {
    height: 100%;
    width: 100%;
    background-color: #1f2235;
}

.project-navbar-item {
    height: 100%;
    width: 100%;
    /* border: 1px solid black; */
    display: flex;
    align-items: center;
    justify-content: flex-start;
    /* background-color: white; */
}

.project-navbar-item span {
    cursor: pointer;
}

.project-navbar-item .project-navbar-text {
    /* border: 1px solid black; */
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 70px;
    background-color: white;
}

.project-navbar-item .project-navbar-img {
    height: 100%;
    width: fit-content;
    align-self: stretch;
    background-color: transparent;
    padding-top: 1rem;
}

.project-navbar-item .project-navbar-img img {
    height: 30px;
    width: 30px;
    background-color: transparent;
    margin: 0.3rem;
}

.project-navbar-item span {
    padding: 0.8rem 0.4rem;
    padding-right: 0.8rem;
}

.project-navbar-item .project-navbar-text .active {
    background-color: #1f2235;
    color: white;
    width: fit-content;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
}

.project-navbar-item:last-child span:first-child {
    padding-bottom: 1.2rem;
}

@media screen and (max-width:830px) {
    .project-navbar {
        width: 100%;
    }
}