.contact-me-header {
    margin: 1rem auto;
    width: fit-content;
    display: flex;
    flex-direction: column;
    color: white;
}

.contact-me-header-top {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.contact-me-header .contact-me-header-top span {
    height: 5px;
    width: 3rem;
    margin-left: 0.3rem;
    background-color: white;
}


/* Map styling starts here */

.mapouter {
    position: relative;
    text-align: right;
    width: 30%;
    height: auto;
}

.gmap_canvas {
    overflow: hidden;
    background: none!important;
    width: 100%;
    height: 100%;
}

.gmap_iframe {
    width: 100%!important;
    height: 400px!important;
}


/* Map styling ends here */

.current-location {
    color: white;
    /* border: 1px solid white; */
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

.current-location .address {
    direction: ltr;
    text-align: start;
}

.current-location .pincode {
    text-align: start;
}

.contact-me-container {
    width: 80%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 1rem auto;
    /* border: 1px solid white; */
}

#message-textarea {
    resize: none;
    resize: vertical;
    overflow-y: auto;
    width: 90%;
    height: 200px;
    min-height: 200px;
    max-height: 400px;
    margin: 0 auto;
    margin-bottom: 1rem;
    background-color: #fffdf4;
    border: none;
    outline: none;
    font-size: 1rem;
    padding: 0.5rem;
    color: black;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}

.contact-me-card {
    width: 70%;
    height: auto;
    padding: 1rem;
}

.contact-me-card form {
    border-radius: 15px;
    flex: 1;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    /* border: 1px solid white; */
    overflow-y: auto;
}

.contact-me-card form label {
    color: white;
    font-weight: bold;
    letter-spacing: 1.2px;
    font-size: 1.2rem;
    margin: 0.5rem 1rem;
}

.contact-me-card form input {
    background-color: #fffdf4;
    color: black;
    font-size: 1rem;
    padding: 0.8rem 1rem;
    border-radius: 15px;
    margin: 0.5rem 1rem;
    outline: none;
    border: none;
}

#send-me-btn {
    align-self: center;
    justify-self: center;
    padding: 1rem 1.5rem;
    border-radius: 20px;
    border: none;
    outline: none;
    font-size: 1.2rem;
    background-color: #F96167;
    color: #FCE77D;
    cursor: pointer;
}

@media screen and (max-width:800px) {
    .contact-me-container {
        flex-direction: column;
        width: 90%;
        border: none;
    }
    .mapouter {
        width: 100%;
    }
    .gmap_iframe {
        height: 300px;
    }
    .contact-me-card {
        width: 100%;
    }
}