#about {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
    color: white;
}

#about .header {
    font-size: 2rem;
    margin: 2rem;
    background: linear-gradient( to right, #F96167, #F96167, #FCE77D, #FCE77D);
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
}

.about-card {
    /* border: 1px solid white; */
    border-radius: 5px;
    /* overflow-y: auto; */
    overflow: hidden;
    height: 500px;
    max-height: 650px;
    width: 80%;
    display: flex;
    -webkit-box-shadow: 0 0 24px 8px rgb(42, 41, 42);
    -moz-box-shadow: 0 0 24px 8px rgb(42, 41, 42);
    box-shadow: 0 0 24px 8px rgb(42, 41, 42)
}

.image-wrapper {
    height: 100%;
    width: 30%;
    position: relative;
    /* border: 1px solid white; */
}

#me {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-size: cover;
}

.about-details-container {
    flex: 1;
    height: 100%;
    /* border: 1px solid white; */
    padding: 0 calc(3vw + 5px);
    overflow-y: auto;
}

.about-details-container::-webkit-scrollbar {
    width: 5px;
    background-color: transparent;
}

.about-details-container::-webkit-scrollbar-thumb {
    background-color: white;
}

.about-details-container .about-header {
    text-align: center;
    margin: 10px 0;
    font-weight: bold;
    letter-spacing: 1.2px;
    font-size: 1.4rem;
    overflow-y: auto;
}

.about-details-ul {
    list-style-type: circle;
}

.about-contact-me-btn {
    letter-spacing: 1.2px;
    word-spacing: 1.5px;
    background-color: rgb(0, 0, 93);
    color: white;
    font-weight: bold;
    padding: 0.5rem 1rem;
    border-radius: 10px;
    cursor: pointer;
    margin-top: 0.3rem;
    text-align: center;
    width: fit-content;
    margin-bottom: 0.5rem;
}

.about-contact-me-btn a {
    text-decoration: none;
    color: white;
}

.hackathons-awards {
    display: flex;
    align-self: stretch;
    justify-self: center;
    width: 80%;
    margin: 2rem auto;
    height: auto;
    /* border: 1px solid white; */
    justify-content: space-evenly;
    align-items: center;
}

.hackathons-awards .hackathons,
.hackathons-awards .internships {
    width: 40%;
    max-height: 300px;
    overflow-y: auto;
    padding: calc(1vh + 4px) calc(1.5vw + 4px);
    -webkit-box-shadow: 0 0 24px 8px rgb(42, 41, 42);
    -moz-box-shadow: 0 0 24px 8px rgb(42, 41, 42);
    box-shadow: 0 0 24px 8px rgb(42, 41, 42)
}

.hackathons-awards h2 {
    text-align: center;
    margin: 0.5rem 0;
}

.hackathons-awards .internships .internship-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.hackathons-awards .internships .internship-date {
    background-color: #F96167;
    color: white;
    border-radius: 15px;
    padding: 0.5rem;
    font-size: 0.8rem;
}

.hackathons-awards .internships li p {
    margin: 0.5rem 0;
}

.internships::-webkit-scrollbar,
.hackathons::-webkit-scrollbar {
    width: 3px;
    background-color: transparent;
}

.internships::-webkit-scrollbar-thumb,
.hackathons::-webkit-scrollbar {
    background-color: white;
}

@media screen and (max-width:800px) {
    .about-card {
        flex-direction: column;
        width: 85%;
        height: 650px;
    }
    .image-wrapper {
        margin: 0 auto;
        width: 80%;
        height: 50%;
        flex: 1;
    }
    .about-details-ul {
        padding: 1.5vw;
        font-size: 0.9rem;
    }
    .hackathons-awards {
        width: 100%;
        flex-direction: column;
        /* border: 1px solid white; */
    }
    .hackathons-awards .hackathons,
    .hackathons-awards .internships {
        width: 90%;
        margin: calc(1.5vw + 5px);
    }
    .hackathons-awards .hackathons ul,
    .hackathons-awards .internships ul {
        padding: calc(1.5vw + 5px);
    }
}