#footer {
    width: 100%;
    height: auto;
    background-color: rgb(0, 0, 70);
    padding: calc(3vw + 5px);
}

.copyright {
    color: white;
    text-align: center;
    font-size: 0.9rem;
}

.contact-info {
    display: flex;
    justify-content: center;
}

.contact-info img {
    height: 30px;
    width: 30px;
    margin: 0.5rem;
    background-color: rgb(208, 208, 208);
    border-radius: 50%;
    padding: 0.2rem;
}