.project-details {
    align-self: flex-start;
    width: 65%;
    height: 100%;
    color: black;
    padding: 0 calc(3vw + 5px);
    overflow-y: auto;
    /* border: 1px solid red; */
}

.project-details-title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* border: 1px solid red; */
    margin: 1rem 0;
}

.project-details-title h2 {
    color: #F96167;
}

.project-details-description {
    margin-top: 1rem;
}

.project-details-description ul {
    padding: 0.5rem 0;
    font-size: 0.9rem;
}

.project-details-title span {
    padding: 0.5rem 1rem;
    color: white;
    background-color: #F96167;
    border-radius: 20px;
}

.project-link {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin: 0.6rem 0;
}

.project-link a {
    padding: 0.5rem 1rem;
    background-color: #F96167;
    border-radius: 20px;
    color: #FCE77D;
    text-decoration: none;
}

.project-link a:last-child {
    color: white;
    background-color: #1f2235;
}

@media screen and (max-width:830px) {
    .project-details {
        width: 100%;
    }
    .project-link {
        margin-bottom: 1rem;
    }
}