nav {
    background-color: rgba(0, 0, 0, 1);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 2;
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 60px;
    -webkit-box-shadow: 0 0 24px 8px rgb(42, 41, 42);
    -moz-box-shadow: 0 0 24px 8px rgb(42, 41, 42);
    box-shadow: 0 0 24px 8px rgb(42, 41, 42);
}

nav h1 {
    font-size: 1.9rem;
    color: white;
    font-weight: 900;
    letter-spacing: 1.2px;
    font-family: 'Mingzat', sans-serif;
    background: linear-gradient( to right, rgb(100, 100, 200), cyan, white);
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
}

nav .nav-items {
    color: white;
    list-style-type: none;
    display: flex;
}

nav .nav-items li {
    padding: 0.5rem 1rem;
    margin: 0.5rem;
    font-weight: bold;
    letter-spacing: 1.2px;
    position: relative;
    transition: 0.1s all ease-in;
    /* border: 1px solid white; */
}

nav .nav-items li a {
    color: white;
    text-decoration: none;
}

nav .nav-items li::after {
    content: '';
    display: none;
    background-color: white;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 2px;
    width: 100%;
}

nav .nav-items li:hover::after {
    display: block;
    background-color: aqua;
}

nav .nav-items li:hover {
    transform: scale(1.1);
    cursor: pointer;
}

nav .nav-items li a:hover {
    color: aqua;
}

.nav-responsive {
    display: none;
    justify-content: space-between;
    padding: 0 3vw;
    position: relative;
}

.nav-responsive button {
    background: transparent;
    transform: rotateZ(90deg);
    color: white;
    font-size: 1.5rem;
    border: none;
    outline: none;
    cursor: pointer;
}

.nav-items-responsive {
    display: flex;
    flex-direction: column;
    color: white;
    list-style-type: none;
    position: absolute;
    bottom: -190px;
    right: 5px;
    padding: 0 calc(1vw+5px);
    background-color: rgb(1, 1, 79);
}

.nav-items-responsive::before {
    content: '';
    position: absolute;
    top: -10px;
    right: 10px;
    width: 0;
    height: 0;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 10px solid rgb(0, 0, 108);
}

.nav-items-responsive li {
    margin: 0.5rem;
}

.nav-items-responsive li a {
    color: white;
    text-decoration: none;
}

@media screen and (max-width:800px) {
    nav {
        display: none;
    }
    .nav-responsive {
        display: flex;
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
    }
}