#projects {
    color: white;
    background-color: #fffdf4;
    width: 100%;
    height: auto;
    /* border: 1px solid black; */
    display: flex;
    flex-direction: column;
    justify-content: center;
}

#projects h1 {
    font-weight: bold;
    text-align: center;
    margin: 2rem 0;
    font-size: 2rem;
    background: linear-gradient( to right, #F96167, #F96167, #FCE77D, #FCE77D);
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
}

.project-parent-container {
    width: 80%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: white;
    margin: 3rem auto;
    /* border: 1px solid black; */
}

.download-resume {
    background-color: yellow;
    color: red;
    padding: 1rem 1.5rem;
    border-radius: 20px;
    border: none;
    outline: none;
    align-self: center;
    width: fit-content;
    justify-self: center;
    margin-bottom: 2rem;
    /* cursor: pointer; */
}

.download-resume a {
    color: red;
    text-decoration: none;
}

@media screen and (max-width:830px) {
    .project-parent-container {
        flex-direction: column;
    }
}