#carrer {
    height: 100vh;
    width: 100%;
}

#carrer h1 {
    color: white;
    text-align: center;
    margin-bottom: 2rem;
    font-size: 2rem;
    background: linear-gradient( to right, #F96167, #F96167, #FCE77D, #FCE77D);
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
}

.carrer-container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: auto;
    width: auto;
}

.Barchart-container {
    background-color: white;
    height: auto;
    width: 70%;
    color: black;
    margin: 0 auto;
    -webkit-box-shadow: 0 0 24px 8px rgb(42, 41, 42);
    -moz-box-shadow: 0 0 24px 8px rgb(42, 41, 42);
    box-shadow: 0 0 24px 8px rgb(42, 41, 42)
}

.carrier-insights {
    color: white;
    display: flex;
    flex-direction: column;
    padding: 0 calc(2vw + 5px);
}

table {
    border: 1px solid white;
    border-collapse: collapse;
    margin: 0.2rem;
}

table tr {
    border: 1px solid white;
}

table thead th {
    padding: 0.5rem 1rem;
}

table tbody tr td {
    text-align: center;
}

table tbody tr td:first-child {
    background-color: #101820FF;
    color: #FEE715FF;
}

table tbody tr td:last-child {
    background-color: #FEE715FF;
    color: #101820FF;
    font-weight: bold;
}

table tbody tr:last-child td:first-child {
    background-color: #F96167;
    color: #FCE77D;
    font-weight: bold;
}

table tbody tr:last-child td:last-child {
    background-color: #FCE77D;
    color: #F96167;
    font-weight: bold;
}

@media screen and (max-width:650px) {
    #carrer {
        height: auto;
    }
    .Barchart-container {
        height: auto;
        width: 90%;
    }
    .carrer-container {
        flex-direction: column;
    }
    .carrier-insights {
        margin: 1rem 0;
        flex-direction: row;
    }
    .carrier-insights table {
        font-size: 0.9rem;
    }
}