.parent-container {
    position: relative;
    width: 100%;
    height: 100vh;
    font-family: 'Poppins', sans-serif;
    scroll-behavior: smooth;
}

.parent-container #background-image {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
    background-size: cover;
    filter: brightness(0.7);
}


/* nav bar starts here */


/* Nav bar ends here */


/* Home section starts here */

#home {
    color: white;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: row-reverse;
    height: 100vh;
    width: 100%;
    font-family: 'Mukta', sans-serif;
}

#home .date-container .date {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: -40px;
    /* border: 1px solid white; */
}

#home .date-container .date .day {
    font-size: 8rem;
    vertical-align: text-top;
    /* border: 1px solid white; */
}

#home .date-container .date .others {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    font-size: 2rem;
}

.date .others .time {
    width: 10rem;
    /* border: 1px solid white; */
    display: block;
}

#home .header {
    font-size: 1.2rem;
}

.personal-details h2 {
    font-size: 3rem;
    text-align: center;
}

.personal-details {
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.skills-container {
    display: flex;
    font-size: 1.5rem;
    width: max-content;
    display: flex;
    justify-content: center;
    align-items: center;
    /* border: 1px solid white; */
    height: 3rem;
    width: 100%;
}

.skills {
    /* border: 1px solid white; */
    margin-left: 0.5rem;
    width: fit-content;
    word-wrap: break-word;
    font-size: 3rem;
    display: flex;
    background: linear-gradient( to right, #FF69B4, #FF69B4, #00FFFF, #00FFFF);
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
}

.skills-highlight {
    margin-top: 1.5rem;
    text-align: center;
}

.skills #text_cursor {
    animation-timing-function: ease;
    animation-duration: 300ms;
    animation-name: blinker;
}

.contact-me-btn {
    letter-spacing: 1.2px;
    word-spacing: 1.5px;
    background-color: rgb(30, 30, 123);
    color: white;
    font-weight: bold;
    padding: 0.5rem 1rem;
    border-radius: 10px;
    cursor: pointer;
    margin-top: 0.3rem;
}

.contact-me-btn a {
    font-size: 1.5rem;
    color: white;
    text-decoration: none;
}

@keyframes blinker {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@media screen and (max-width:800px) {
    #background-image {
        filter: brightness(1);
    }
    #home {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        height: auto;
    }
    .skills {
        font-size: 2rem;
    }
    #home .date-container {
        margin-top: 4vh;
        height: fit-content;
        z-index: 1;
        /* border: 1px solid red; */
    }
    #home .personal-details {
        margin: 2rem;
        width: 100%;
        /* border: 1px solid red; */
    }
    #home .personal-details .skills-highlight {
        padding: calc(2vw + 5px);
    }
}